//spinner
:host {
  display: flex;
}
.non-overlay {
  display: flex;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}
.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #fff;
  border-color: #fff transparent #fff transparent;
  animation: lds-dual-ring 1s linear infinite;
}
@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loader {
  animation: rotate 1s infinite;
  height: 50px;
  width: 50px;
}
.loader:before,
.loader:after {
  content: '';
  display: block;
  height: 20px;
  width: 20px;
}
.loader:before {
  animation: box1 1s infinite;
  background-color: #21b9d9;
  box-shadow: 30px 0 0 #041361;
  margin-bottom: 10px;
}
.loader:after {
  animation: box2 1s infinite;
  background-color: #041361;
  box-shadow: 30px 0 0 #21b9d9;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg) scale(0.8);
  }
  50% {
    transform: rotate(360deg) scale(1.2);
  }
  100% {
    transform: rotate(720deg) scale(0.8);
  }
}
@keyframes box1 {
  0% {
    box-shadow: 30px 0 0 #041361;
  }
  50% {
    box-shadow: 0 0 0 #041361;
    margin-bottom: 0;
    transform: translate(15px, 15px);
  }
  100% {
    box-shadow: 30px 0 0 #041361;
    margin-bottom: 10px;
  }
}
@keyframes box2 {
  0% {
    box-shadow: 30px 0 0 #21b9d9;
  }
  50% {
    box-shadow: 0 0 0 #21b9d9;
    margin-top: -20px;
    transform: translate(15px, 15px);
  }
  100% {
    box-shadow: 30px 0 0 #21b9d9;
    margin-top: 0;
  }
}
